import createActivityTimer from 'jump-common/timers/createActivityTimer'
import JumpUnity from './JumpUnity'

if (typeof window !== 'undefined') {
	if (window.self === window.top) window.location = 'host/index.html#/'
	else window.jump = new JumpUnity({ createActivityTimer })
}



