import Jump from 'jump-plugin/src/Jump'

export default class JumpUnity extends Jump {
	width
	height
	game

	onInit = x => this.game.SendMessage('JumpPlugin', 'Init', x.userId)
	onFullscreen = x => this.game.SendMessage('JumpPlugin', 'Fullscreen', x.toString())
	onUnityLoaderProgress = (x, y) => this.progress(y)

	start() {
		const { width, height } = this
		super.start({ width, height })
	}

	load({ url, width, height }) {
		this.width = width
		this.height = height
		this.on('init', this.onInit)
		this.on('fullscreen', this.onFullscreen)
		this.game = UnityLoader.instantiate('game', url, {
			onProgress: this.onUnityLoaderProgress
		})
	}
}



